import loaderm from '~/mixins/loader.js'
export default {
  mixins: [loaderm],
  props: {
    media: {
      type: Object,
      default: null,
    },
  },

  data() {
    return {
      vfOptions: {
        autoplay: true,
      },
      vfImages: this.media.imagenes.map((x) => {
        return x.filename
      }),
      vfTransitions: [
        {
          name: 'swipe',
          options: {
            totalDuration: 800,
            easing: 'ease-in-out',
          },
        },
      ],
    }
  },
  mounted() {
    if (window) {
      window.dispatchEvent(new Event('resize'))
    }
  },
}
